import React, { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination, Autoplay } from "swiper";
import HomeData from "../../../data/home.json";
import Team from "../../../components/team";
import SectionTitle from "../../../components/section-title";
import Resume from "../../../components/resume";

SwiperCore.use([Pagination, Autoplay]);

const TeamContainer = () => {
    const [selectedTeamMember, setSelectedTeamMember] = useState(null);

    const handleOpenModal = (data) => {
        setSelectedTeamMember(data);
    };

    const handleCloseModal = () => {
        setSelectedTeamMember(null);
    };

    const swiperOption = {
        loop: true,
        speed: 4000,
        spaceBetween: 30,
        slidesPerView: 3,
        slidesPerGroup: 3,
        pagination: { type: "fraction" },
        autoplay: { delay: 3500 },
        breakpoints: {
            1200: { slidesPerView: 3, slidesPerGroup: 3 },
            991: { slidesPerView: 3, slidesPerGroup: 3 },
            767: { slidesPerView: 3, slidesPerGroup: 3 },
            560: { slidesPerView: 3, slidesPerGroup: 3 },
            0: { slidesPerView: 1, slidesPerGroup: 1 },
        },
    };

    return (
        <div className="team-area team-default-area bg-gray">
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <SectionTitle
                            classOption="text-center"
                            subTitle="Ekibimizle Tanışın"
                            title="<span>Profesyonel </span> Ekip Arkadaşlarımız"
                        />
                    </div>
                </div>
                <div className="row">
                    <div
                        className="col-lg-12"
                        data-aos="fade-up"
                        data-aos-duration="1300"
                    >
                        <Swiper
                            className="team-slider-container"
                            {...swiperOption}
                        >
                            {HomeData[4].team &&
                                HomeData[4].team.map((single, key) => (
                                    <SwiperSlide key={key}>
                                        <Team
                                            key={key}
                                            data={single}
                                            onOpenModal={handleOpenModal}
                                        />
                                    </SwiperSlide>
                                ))}
                        </Swiper>
                    </div>
                </div>
            </div>
            {selectedTeamMember && (
                <Resume
                    data={selectedTeamMember}
                    isOpen={!!selectedTeamMember}
                    onClose={handleCloseModal}
                />
            )}
        </div>
    );
};

export default TeamContainer;
