import { useForm } from "react-hook-form";
import SidebarTitle from "../sidebar-title";

const Appointment = () => {
    const { register, handleSubmit, errors } = useForm({
        mode: "onBlur",
    });
    const onSubmit = (data) => console.log(data);
    return (
        <div className="widget-item">
            <div className="widget-appointment-form">
                <SidebarTitle classOption="title" title="Randevu Talebi" />
                <form onSubmit={handleSubmit(onSubmit)} method="POST">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="form-group">
                                <input
                                    className="form-control"
                                    type="text"
                                    name="name"
                                    placeholder="İsim *"
                                    ref={register({
                                        required: "İsim gereklidir",
                                    })}
                                />
                                {errors.name && <p>{errors.name.message}</p>}
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group">
                                <input
                                    className="form-control"
                                    type="email"
                                    name="email"
                                    placeholder="Eposta *"
                                    ref={register({
                                        required: "Eposta gereklidir",
                                        pattern: {
                                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                                            message: "Geçersiz eposta adresi",
                                        },
                                    })}
                                />
                                {errors.email && <p>{errors.email.message}</p>}
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group">
                                <input
                                    className="form-control"
                                    type="text"
                                    name="phone"
                                    placeholder="Telefon numarası *"
                                    ref={register({
                                        required: "Telefon numarası gereklidir",
                                    })}
                                />
                                {errors.phone && <p>{errors.phone.message}</p>}
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group datepicker-group">
                                <label
                                    htmlFor="datepicker"
                                    className="form-label icofont-calendar"
                                >
                                    <input
                                        className="form-control"
                                        id="datepicker"
                                        type="date"
                                        name="date"
                                        placeholder="Date"
                                        ref={register({
                                            required: "Tarih seçiniz",
                                        })}
                                    />
                                </label>
                                {errors.date && <p>{errors.date.message}</p>}
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-0">
                                <textarea
                                    name="message"
                                    rows="4"
                                    placeholder="Lütfen detayları yazınız. Randevu için size geri dönüş yapılacaktır."
                                    ref={register({
                                        required: "Mesaj gereklidir",
                                    })}
                                ></textarea>
                                {errors.message && (
                                    <p>{errors.message.message}</p>
                                )}
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-0">
                                <button className="btn btn-theme" type="submit">
                                    Talep Oluştur
                                </button>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    );
};

export default Appointment;
